*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family:cursive;
}
::-webkit-scrollbar{
    width: 10px;
    border-radius: 5px;
}
::-webkit-scrollbar-thumb{
    background: rgb(15, 63, 219);
}
::-webkit-scrollbar-thumb:hover{
    background: rgb(35, 54, 117);
}
.App{
    padding: 1rem;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    height: 100vh;
}
.header{
    background-color: rgb(15, 63, 219);
    width: 100%; 
    color: white;
    padding: 1rem;
}
.items{
    flex-grow: 1;
    margin-top: 10px;
    align-items: center;
    font-size: 1.6rem;
    width: 100%;
    overflow-x: auto;
}
.item input{
    width: 2rem;
    height: 2rem;
    margin-right: 15px;
    
}

.items li{
    list-style: none;
    display: flex;
    padding: 1rem;
}
.items li:nth-child(odd){
    background-color: rgb(182, 177, 177);
}
.item {
    display: flex;
}
.item p{
    width:200px ;
    flex-grow:1;
}
.item .btn{
    color: rgb(15, 63, 219);
}
.item .btn:hover{
    color: rgb(233, 7, 7);
}
.new-item{
    width: 100%;
}
.new-item input{
    width: 100%;
    padding: .6rem;
    align-items: flex-start;
    margin-top: .4rem;
}
.footer{
    width: 100%;
    padding: 1rem;
    text-align: center;
    color: white;
    background-color:rgb(15, 63, 219); ;
}
